****************************************************************/
/* Padding */
.content-pad-sm {
  padding-top: 3%;
  padding-bottom: 3%; }

.content-pad-md {
  padding-top: 6%;
  padding-bottom: 6%; }

.content-pad-lg {
  padding-top: 12%;
  padding-bottom: 12%; }

.content-pad-xlg {
  padding-top: 18%;
  padding-bottom: 18%; }

/****************************************************************
 ALIGNMENT
****************************************************************/
.alignnone {
  float: none;
  display: block;
  margin: 3rem auto; }

.alignleft {
  float: left;
  margin: 0 2rem 1rem 0; }

.alignright {
  float: right;
  margin: 0 0 1rem 2rem; }

.aligncenter {
  display: block;
  margin: 2rem auto !important; }

.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw; }

.alignwide {
  margin: 0 calc(25% - 25vw); }

/****************************************************************
 WIDTH
****************************************************************/
.max-width-sm {
  max-width: 540px; }

